@import url(https://fonts.googleapis.com/css2?family=Grandstander:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: 'Grandstander', cursive;
}
.App{
    background:url('https://www.lefthudson.com/wp-content/uploads/2019/11/white-wallpaper-1920x1080-unique-black-and-white-abstract-backgrounds-57-images-combination-of-white-wallpaper-1920x1080.jpg') no-repeat fixed;
    display: flex;
    align-items: center;
    justify-content: center;
}
.custom{ 
    
    margin-top: 20%;
    box-shadow: 0px 5px 10px rgb(0, 0, 0);
    text-align: center;    
}
.Charts{
    width:100%;
    height: 80%;
    margin-top: 10%;
    margin-left: auto;
    margin-right: auto;
}
@media (max-width: 576px) {
    .App{
        background:url('https://www.lefthudson.com/wp-content/uploads/2019/11/white-wallpaper-1920x1080-unique-black-and-white-abstract-backgrounds-57-images-combination-of-white-wallpaper-1920x1080.jpg') no-repeat fixed;
     
    }
  }
  
  /* Medium devices (tablets, 768px and up) */
  @media (max-width: 768px) { 
    .App{
        background:url('https://www.lefthudson.com/wp-content/uploads/2019/11/white-wallpaper-1920x1080-unique-black-and-white-abstract-backgrounds-57-images-combination-of-white-wallpaper-1920x1080.jpg') no-repeat fixed;
     
    }
  }
